import React, { useState } from 'react';
import Box from "@mui/material/Box";
import Typography from '@mui/material/Typography';
import Switch from '@mui/material/Switch';
import FormControlLabel from '@mui/material/FormControlLabel';
import Alert from '@mui/material/Alert';
import AlertTitle from '@mui/material/AlertTitle';
import IconButton from '@mui/material/IconButton';
import Grid from '@mui/material/Grid';
import HelpOutlineIcon from '@mui/icons-material/HelpOutline';
import CircularProgress from '@mui/material/CircularProgress';


const Settings = (props) => {

	const [msgServiceAlertClicked, setMsgServiceAlert] = useState(false);
	const [carrierTypeAlertClicked, setCarrierType] = useState(false);
	const [carrierSwitch, setCarrierSwitch] = useState(false);
	const [msgServiceSwitch, setMsgServiceSwitch] = useState(false);

	function handleCarrierSwitch() {
		setCarrierSwitch(carrierSwitch ? false : true)
		props.carrierTypeSwitchCallback(!carrierSwitch)
	}

	function handleMsgServiceSwitch() {
		setMsgServiceSwitch(msgServiceSwitch ? false : true)
		props.msgServiceSwitchCallback(!msgServiceSwitch)
	}

	return(
		<Box mt={2}>

      <Typography variant="h5" component="h1" gutterBottom>
        Settings
      </Typography>

      <Box mt={1}>
        <FormControlLabel control={<Switch disabled={props.channelRadioButton === "Whatsapp" ? true : false || !props.isAuthenticated} checked={carrierSwitch} onChange={handleCarrierSwitch} />} label="Check Carrier Type" />
        <IconButton onClick={() => setCarrierType(carrierTypeAlertClicked ? false : true)}>
          <HelpOutlineIcon/>
        </IconButton>
        </Box>
        { carrierTypeAlertClicked ?
        (<Alert severity="info">
          <AlertTitle>About Carrier Type</AlertTitle>
          Looking for carrier type can provide additional insights on the number itself. For example you can exclude landlines or VOIP numbers from your SMS campaigns. Not needed for Whatsapp campaigns.
          Additional charges apply. More information <a href="https://www.twilio.com/docs/lookup/api#lookups-carrier-info" target="_blank">here</a>
        </Alert>)
        : ""
        }

        <Box mt={1}>
          <FormControlLabel control={<Switch checked={msgServiceSwitch} onChange={handleMsgServiceSwitch} disabled={!props.isAuthenticated} />} label="Use Messaging Service" />
          <IconButton onClick={() => setMsgServiceAlert(msgServiceAlertClicked ? false : true)}>
            <HelpOutlineIcon/>
          </IconButton>
          {
            props.showServicesLoadingIcon ?

            (<>
              <CircularProgress size={15}/>
              <Typography variant="h6"gutterBottom>
                Loading Messaging Services...
              </Typography>

            </>) : ""
          }
        </Box>
        { msgServiceAlertClicked ?
        (<Alert severity="info">
          If selected, you will get a dropdown with the available Messaging Services you have created in your account.
          You can choose one of them for the outbound messaging. You can read more about messaging services <a href="https://www.twilio.com/docs/messaging/services/tutorials/how-to-send-sms-messages-services" target="_blank">here</a>
        </Alert>)
        : ""
        }
        
      </Box>
     )

}

export default Settings;